import http from '../../http'
import router from '../../router'

const state = {
    loginUsername: null,
    loginPassword: null,
    registerUsername: null,
    registerPassword: null,
    registerPassword2: null,
    loginError: null,
    registerError: null,
    token: null
}

// getters
const getters = {}

// actions
const actions = {
    login ({state, commit}) {
        return http().post('/auth/login', {
            username: state.loginUsername,
            password: state.loginPassword
        }).then((res) => {
            commit('setToken', res.data.token)
            commit('updateLoginPassword', null)
            router.push('/home')
        }).catch(({response}) => {
            let msg = response.data[0].message
            if (msg === 'Cannot find user with provided username') {
                commit('setLoginError', '用户名不存在')
            } else if (msg === 'Invalid user password') {
                commit('setLoginError', '密码错误')
            }
        })
    },
    register ({state, commit}) {
        if (state.registerPassword !== state.registerPassword2) {
            commit('setRegisterError', '密码需一致')
            return
        }
        return http().post('/auth/register', {
            username: state.registerUsername,
            password: state.registerPassword
        }).then((res) => {
            commit('setToken', res.data.token)
            router.push('/home')
        }).catch((err) => {
            commit('setRegisterError', '账号已存在')
        })
    }
}

const mutations = {
    updateLoginUsername (state, value) {
        state.loginUsername = value
    },
    updateLoginPassword (state, value) {
        state.loginPassword = value
    },
    updateRegisterUsername (state, value) {
        state.registerUsername = value
    },
    updateRegisterPassword (state, value) {
        state.registerPassword = value
    },
    updateRegisterPassword2 (state, value) {
        state.registerPassword2 = value
    },
    setToken (state, token) {
        state.token = token
    },
    setLoginError (state, msg) {
        state.loginError = msg
    },
    setRegisterError (state, msg) {
        state.registerError = msg
    },
    logout (state) {
      state.token = null
      router.push('/')
    }
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}