<template>
  <div id="login">
    <header>卓绝智能</header>
    <b-container class="wrapper">
      <b-row>
        <b-col class="tabs">
          <div
            class="tab"
            @click="tabIndex = 1"
            :style="tabIndex === 1 ? activeStyle : nonactiveStyle"
          >登录</div>
          <div
            class="tab"
            @click="tabIndex = 2"
            :style="tabIndex === 2 ? activeStyle : nonactiveStyle"
          >注册</div>
        </b-col>
      </b-row>
      
      <b-row>
        <b-col lg="4" md="8">
          <div class="content" v-if="tabIndex === 1" key="field1">
            <input
              type="text"
              placeholder="用户名"
              key="user1"
              :value="loginUsername"
              @input="updateUser"
            >
            <br>
            <input
              type="password"
              placeholder="密码"
              key="pwd1"
              :value="loginPassword"
              @input="updatePassword"
              @keyup.enter="handleLogin"
            >
            <b-alert variant="warning" class="mt-4" :show="loginError !== null">{{loginError}}</b-alert>
            <b-button class="mt-4" variant="primary" @click="handleLogin">登录</b-button>
          </div>

          <div class="content" v-if="tabIndex === 2" key="field2">
            <input
              type="text"
              placeholder="用户名"
              key="user2"
              :value="registerUsername"
              @input="updateUser1"
            >
            <br><br>
            <input
              type="password"
              placeholder="密码"
              key="pwd1"
              :value="registerPassword"
              @input="updatePassword1"
            >
            <br><br>
            <input
              type="password"
              placeholder="再次输入密码"
              key="pwd2"
              :value="registerPassword2"
              @input="updatePassword2"
            >
            <b-alert variant="warning" class="mt-4" :show="registerError !== null">{{registerError}}</b-alert>
            <b-button class="mt-4" variant="primary" @click="handleRegister">创建新帐号</b-button>
          </div>
        </b-col>
      </b-row>
    </b-container>
  </div>
</template>

<script>
import { mapMutations, mapState, mapActions } from 'vuex';
export default {
  name: 'Login',
  data () {
    return {
      // 切换登录，注册
      tabIndex: 1,
      // 切换登录，注册的样式
      activeStyle: {
        borderBottom: '1px solid #6459ed',
        color: '#6459ed'
      },
      nonactiveStyle: {
        borderBottom: 'none',
        color: '#000000'
      }
    }
  },
  computed: {
    ...mapState(('login'), {
      loginUsername: state => state.loginUsername,
      loginPassword: state => state.loginPassword,
      registerUsername: state => state.registerUsername,
      registerPassword: state => state.registerPassword,
      registerPassword2: state => state.registerPassword2,
      loginError: state => state.loginError,
      registerError: state => state.registerError
    }),
  },
  methods: {
    ...mapActions(('login'), [
      'login',
      'register'
    ]),
    ...mapMutations(('login'), [
      'updateLoginUsername',
      'updateLoginPassword',
      'updateRegisterUsername',
      'updateRegisterPassword',
      'updateRegisterPassword2',
    ]),
    updateUser (e) {
      this.updateLoginUsername(e.target.value)
    },
    updatePassword (e) {
      this.updateLoginPassword(e.target.value)
    },
    updateUser1 (e) {
      this.updateRegisterUsername(e.target.value)
    },
    updatePassword1 (e) {
      this.updateRegisterPassword(e.target.value)
    },
    updatePassword2 (e) {
      this.updateRegisterPassword2(e.target.value)
    },
    handleLogin(){
      this.login()
    },
    handleRegister() {
      this.register()
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '~@/assets/css/variables.styl'
#login
  position relative
  width 100%
  height 100%
  font-size 20px
  header
    height 100px
    line-height 120px
    padding-left 165px
    color #ffffff
    background #6459ed
    background-image url('~@/assets/image/logo.png')
    background-repeat no-repeat
    background-size auto 70px
    background-position 100px center
  .wrapper
    padding 100px 0 0
    height 100%
    background #ffffff
    .tabs
      display flex
      flex-flow row nowrap
      // justify-content center
      margin-bottom 50px
      .tab
        padding 0 0 10px 0
        margin 0px 40px 0px 0px
        cursor pointer
    .content
      height 100%
      display flex
      flex-flow column nowrap
      input
        border-bottom 1px solid #000000
</style>
