import http from '../../http'
import router from '../../router'

const state = {
  deviceList: null
}

// getters
const getters = {}

// actions
const actions = {
  getDevices ({state, commit}) {
    return http().get('/devices')
      .then(( {data} ) => {
        console.log(data)
        commit('setDeviceList', data)
      })
      .catch(err => {
        console.log(err)
      })
  }
}

const mutations = {
  setDeviceList(state, data) {
    state.deviceList = data
  } 
}

export default {
    namespaced: true,
    state,
    getters,
    actions,
    mutations
}